import { Injectable } from '@angular/core';
import { AngularFireStorage } from 'angularfire2/storage';
import { Observable } from 'rxjs';
import { AngularFireDatabase } from 'angularfire2/database';
import { Chapter } from './model/chapter';

@Injectable({
  providedIn: 'root'
})
export class SlideshowService {

  currentPage: number;
  pageMax: number;
  pageMinChapter: number;
  pageMaxChapter: number;
  currentChapter: number;
  currentChapterTitle: String;
  chaptersList: Chapter[];
  imageUrl: Observable<string | null>;
  nextImageUrl: Observable<string | null>;

  constructor(private afStorage: AngularFireStorage, private afDatabase: AngularFireDatabase) {
    this.currentPage = 1;
    this.currentChapter = 1;
    this.pageMax = 0;
    this.setImageUrl();
    this.retrieveData();
  }




  retrieveData() {
    this.afDatabase.list<Chapter>('/chapters').valueChanges()   // returns observable
      .subscribe(list => {
        this.chaptersList = list;
        this.computePageMax();
        this.setChapterTitle();
      });
  }


  public computePageMax() {
    for (let chapter of this.chaptersList) {
      if (chapter.pageMax > this.pageMax) {
        this.pageMax = chapter.pageMax;
      }
    }
  }

  public setChapterTitle() {
    if (this.chaptersList) {
      for (let chapter of this.chaptersList) {
        if (chapter.pageMin <= this.currentPage && this.currentPage <= chapter.pageMax) {
          this.currentChapterTitle = chapter.title;
          this.pageMinChapter = chapter.pageMin;
          this.pageMaxChapter = chapter.pageMax;
        }
      }
    }
  }

  public nextPage() {
    this.currentPage = this.currentPage + 1;
    this.computePage();
  }

  public previousPage() {
    this.currentPage = this.currentPage - 1;
    this.computePage();
  }

  computePage() {
    this.checkChapter();
    this.setImageUrl();
  }

  checkChapter() {
    if ((this.currentPage > this.pageMaxChapter) || (this.currentPage < this.pageMinChapter)) {
      this.setChapterTitle();
    }
  }

  public isLastPage() {
    return this.currentPage == this.pageMax;
  }
  setImageUrl() {
    const ref = this.afStorage.ref(this.currentPage + '.jpg');
    this.imageUrl = ref.getDownloadURL();
  }

}
